import React from 'react';
import './ForgotEmail.scss';
import ReCAPTCHA from 'react-google-recaptcha';
import { Field, reduxForm } from 'redux-form';
import _ from 'lodash';

import config from 'Config';
import logoImg from '../assets/droplet-logo.png';
import logOutImg from '../assets/logout-icon.png';
import notRobotImg from '../assets/not-robot-img.png';

class ForgotEmailContainer extends React.Component{
  constructor(props){
    super(props);
    const { handleSubmit } = this.props;
  }

  handleSubmit = values => {
    values.preventDefault();
    const { touch} = this.props;
    const errors= validate(values);
    if(errors){
      _.forEach(errors, (msg,field) => {
        touch(field);
      });
    }
  }

  renderField = ({input,label,type,placeholder, meta: { touched, error, warning }}) => (
    <div className="form-group">
      <label htmlFor="rePass">{label}</label>
      <input {...input} className="form-control" placeholder={placeholder} type={type} />
      {touched && error && <span className="error-msg-text">{error}</span>}

    </div>
)

  goToIndex = () => {
      this.props.router.push('/');
  }

  goToLogin = () => {
    this.props.router.push('/login');
  }

  goToForgotPassword = () => {
    this.props.router.push('/forgot-password');
  }

  onRecaptcha = () => {

  }

  renderRecaptchaField(field) {
    const { meta: { touched, error } } = field;
    return (
      <div>
        <ReCAPTCHA
          sitekey={config.ReCAPTCHA_KEY}
          onChange={field.input.onChange}
        />
        <div><p className="text-danger">{ touched ? error : '' }</p></div>
      </div>
    );
  }

  render() {
      return (
          <div className="forgot-form">
              <div className="header-section">
                  <div className="header-items">
                      <div className="header-left">
                          <img src={logoImg} className="droplet-logo" onClick={this.goToIndex}/>
                      </div>
                      <div className="header-right">
                          <label className="header-text pointer" onClick={this.goToLogin}>Login</label>
                      </div>
                  </div>
              </div>

              <div className="container history-container">
                  <div className="row">
                      <div className="offset-1 col-10">
                          <div className="step-section">
                              <h4 className="step-text">Forgot Password?</h4>
                          </div>
                          <div className="password-section">
                          <form onSubmit={this.handleSubmit}>
                            <div className="form-group">
                              <Field className="form-control" placeholder="elsonng@gmail.com" label="Your Email" name="email" component={this.renderField} type="email" />
                            </div>
                              <div>
                                  <Field name="recaptcha" component={this.renderRecaptchaField} />
                              </div>
                              <button onClick={this.goToForgotPassword} className="btn btn-green btn-checkout">RESET AND LOGIN</button>
                          </form>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      );
  }

}

const validate = values => {
  const errors = {}
  if (!values.email) {
    errors.email = 'Required'
  } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
    errors.email = 'Invalid email address'
  }
  if (!values.recaptcha) {
    errors.recaptcha = 'reCAPTCHA required.';
  }
  return errors
}

export default reduxForm({form: 'forgotEmail',validate})(ForgotEmailContainer)



